html {
  box-sizing: border-box;
  font-size: 16px;
  font-family: sans-serif;
  background-color: #f8f8ff;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

#title-principal {
  margin: 0;
  text-align: center;
  font-size: 3rem;
}

.section-note {
  width: 100%;
  display: flex;
  justify-content: center;
}
.container-note {
  display: flex;
  flex-direction: column;
  margin: 1rem;
  padding: 1rem;
  width: 500px;
  background-color: #84b6f4;
  border-radius: 15px;
  box-shadow: 5px 4px 10px 1px #00000040;
}
input {
  padding: 0;
  border: none;
  background-color: #84b6f4;
  outline: none;
  font-size: 1.3rem;
  font-weight: bold;
}
textarea {
  margin-top: 0.5rem;
  font-family: sans-serif;
  resize: none;
  font-size: 1rem;
  border: none;
  background-color: #84b6f4;
  outline: none;
}
.container-tools {
  display: flex;
  justify-content: space-between;
}
.container-btn {
  display: flex;
  justify-content: flex-end;
}
.btn-add {
  display: flex;
  padding: 0;
  width: 2rem;
  height: 2rem;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  border: none;
  border-radius: 20px;
  cursor: pointer;

  box-shadow: 3px 3px 8px 1px #00000020;
}

/*Section More Notes*/

.section-more-notes {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.container-info {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin: 1rem;
  padding: 1rem;
  border-radius: 15px;
  background-color: #84b6f4;
  box-shadow: 5px 4px 10px 1px #00000040;
}
.container-info > h3 {
  margin: 0;
  font-size: 1.2rem;
  border-bottom: thin solid black;
}

.container-info > p {
  margin: 0;
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-size: 1rem;
  word-wrap: break-word;
}
.container-info > div {
  display: flex;
  justify-content: flex-end;
}
#btn-remove,
#btn-edit {
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  margin-right: 0.5rem;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 3px 3px 8px 1px #00000020;
  background-color: #fff;
}
#btn-remove:hover,
#btn-edit:hover {
  background-color: #cbcbcb;
}

#title-default {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
}
@media screen and (min-width: 500px) {
  .section-more-notes {
    display: grid;
    grid-template-columns: repeat(2, 250px);
  }
  #title-default {
    grid-column: 1/3;
  }
}

@media screen and (min-width: 750px) {
  .section-more-notes {
    display: grid;
    grid-template-columns: repeat(3, 350px);
  }
  #title-default {
    grid-column: 1/4;
  }
}
